.alert {
  box-shadow: 0 5px 14px 0 rgba(0, 0, 0, 0.1);
  border-radius: 0;
  border: none;
  background: #fff;
  padding: 26px 31px;
  border-left: 3px solid;
  margin: 0 auto 20px auto;
  position: relative; }
  .alert > .start-icon {
    margin-right: 0;
    min-width: 20px;
    text-align: center; }
    @media screen and (min-width: 576px) {
      .alert > .start-icon {
        margin-right: 25px; }
        [dir="rtl"] .alert > .start-icon {
          margin-right: 0;
          margin-left: 25px; } }
  .alert > a {
    font-weight: 600; }
  .alert .close {
    opacity: 0.5;
    line-height: 26px;
    vertical-align: middle;
    box-shadow: none;
    text-shadow: none; }
    .alert .close:focus, .alert .close:hover {
      color: inherit;
      opacity: 1; }
  .alert.fade {
    will-change: opacity;
    transition: opacity .3s ease-in-out; }
  .alert.show {
    transition: opacity .15s linear; }
  .alert-simple {
    color: #fff; }
    .alert-simple.alert-success {
      border-color: #8cc54f;
      color: #8cc54f; }
    .alert-simple.alert-info {
